import React from "react"
import {Col,Button,Form} from "react-bootstrap"
import * as contactStyles from "./contact.module.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope, faPhoneAlt, faCheck } from "@fortawesome/free-solid-svg-icons";
import $ from "jquery";
import { Formik } from 'formik';
import * as yup from 'yup';
import { StaticImage } from "gatsby-plugin-image";


export default class Contact extends React.Component {
    constructor(props) {
        super(props);
    }

    goToPage(){

    }

    handleSubmit(e){
        const form = e.currentTarget;
        e.preventDefault();
        console.log($("#formGridName").val())
        console.log($("#formGridEmail").val())
        console.log($("#formGridMessage").val())
        if (form.checkValidity()) {
            e.stopPropagation();
            $("."+contactStyles.formblock).hide();
            $( "#body_response" ).html("I appreciate you contacting me. I will get back in touch with you soon!");
            $( "."+contactStyles.response_block).show();
        }
        
    }

    render(){
        const schema = yup.object().shape({
            name: yup.string().required(),
            email: yup.string().email('Invalid email address').required(),
            message: yup.string().required(),
          });
        return (
            <div className="mainBody">
                <div className={contactStyles.innerBody}>
		            <div className={`${contactStyles.box_contact} ${contactStyles.block1}`}>
			            <div className={contactStyles.response_block}>				            
                            <FontAwesomeIcon icon={faCheck} className={contactStyles.iconFontAwesome}/>					     				            
				            <p id="body_response">Thanks!!</p>
			            </div>
                        <div className={contactStyles.formblock}>
                            <h2>Contact</h2>
                            <p>Questions? Concerns? Comments?</p>
                            <p>Feel free to send me a message and I will get back as soon as possible.</p>
                            <Formik
                                validationSchema={schema}
                                onSubmit={(values, { setSubmitting }) => {                                                                        
                                    $('#submit_form_btn').prop('value', 'Sending, please wait...');					                                                                         
                                    $('#submit_form_btn').prop('disabled', true);

                                    var url = "https://www.luisingarivera.com/nodeServer/sendMail";
                                    // Send the data using post					
                                    var posting = $.post( url, { name: values["name"] , email:values["email"] , msg:values["message"] } );
                                    posting.done(function( data ) {
                                        $("."+contactStyles.formblock).hide();
                                        $( "#body_response" ).html("I appreciate you contacting me. I will get back in touch with you soon!");
                                        $( "."+contactStyles.response_block).show();
                                        setSubmitting(false);
                                    });
                                    /*
                                    setTimeout(() => {
                                        console.log(JSON.stringify(values, null, 2));
                                        $("."+contactStyles.formblock).hide();
                                        $( "#body_response" ).html("I appreciate you contacting me. I will get back in touch with you soon!");
                                        $( "."+contactStyles.response_block).show();
                                        setSubmitting(false);
                                      }, 2000);
                                    */                             
                                  }}
                                initialValues={{
                                    name: '',
                                    email: '',
                                    message: '',                            
                                }}
                                >
                                {({
                                    handleSubmit,
                                    handleChange,
                                    handleBlur,
                                    values,
                                    touched,
                                    isValid,
                                    errors,
                                }) => (
                                        <Form noValidate onSubmit={handleSubmit}>                            
                                            <Form.Row className={contactStyles.rowFlex}>                                    
                                                <Form.Group as={Col} controlId="validationFormik01">
                                                    <Form.Label>Name</Form.Label>
                                                    <Form.Control required name="name" onChange={handleChange} value={values.name} type="text" placeholder="Name" isValid={touched.name && !errors.name} isInvalid={!!errors.name}/>
                                                    <Form.Control.Feedback type="invalid">
                                                        {errors.name}
                                                    </Form.Control.Feedback>
                                                </Form.Group>
                                                <Form.Group as={Col} controlId="formGridEmail">
                                                    <Form.Label>Email</Form.Label>
                                                    <Form.Control required name="email" onChange={handleChange} type="email" placeholder="Enter email" isValid={touched.email && !errors.email} isInvalid={!!errors.email}/>
                                                    <Form.Control.Feedback type="invalid">
                                                        {errors.email}
                                                    </Form.Control.Feedback>
                                                </Form.Group>
                                            </Form.Row>
                                            <Form.Row>
                                                <Form.Group as={Col} controlId="formGridMessage">
                                                    <Form.Label>Message</Form.Label>
                                                    <Form.Control required name="message" onChange={handleChange} as="textarea" rows={3} isInvalid={!!errors.message}/>
                                                    <Form.Control.Feedback type="invalid">
                                                        {errors.message}
                                                    </Form.Control.Feedback>
                                                </Form.Group>
                                            </Form.Row>
                                            <Form.Row className={contactStyles.rowFlex}>
                                                <Button id="submit_form_btn" variant="primary" type="submit">
                                                    Submit
                                                </Button>
                                            </Form.Row> 
                                        </Form>   
                                    )}
                            </Formik>                        
                        </div>
		            </div>
                    <div className={`${contactStyles.box_contact} ${contactStyles.block2}`}>
                        <div>
                            <h2>Contact information</h2>
                            
                            <div>
                                <FontAwesomeIcon icon={faPhoneAlt}/> (+44) 07821232546					        	
                            </div>
                        
                            <div>
                                <FontAwesomeIcon icon={faEnvelope}/> lair60@yahoo.es					        	
                            </div>                               
                            
                            <a id="linkedin_link" href="https://uk.linkedin.com/in/lair60" onClick={() => {return this.goToPage('MyLinkedin');}} target="_blank">
                                <StaticImage src="../images/my_linkedin_profile_icon.png" alt="Linkedin Icon" layout="fixed"/>                                
                            </a>
                        </div>
                    </div>
	            </div>
            </div>
        )
    }
}